.rt_slides {
	outline: 1px solid green;
	overflow: hidden;
	position: relative;

	& * {
		box-sizing: border-box;
	}
}

.rt_wrapper {
	display: flex;
	transition: 0.5s;
}

.rt_slide {
	flex: 0 0 100%;
	max-width: 100%;
	padding: 1rem;

}

img {
	max-width: 100%;
}

.rt_title {
	font-size: 2rem;
}

.rt_navigation {
	display: flex;
	justify-content: center;
	gap: 1rem;
	position: absolute;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);
}

.rt_nav_left,
.rt_nav_right {
	padding: 0.5rem 1rem;
	color: #fff;
	position: relative;
	cursor: pointer;

	&::before {
		content: '';
		border: 1rem solid transparent;
		position: absolute;
		transition: 0.2s;
	}

	user-select: none;
}

.rt_nav_left {
	&::before {
		border-right: 1rem solid #333;
		left: 0;
	}

	&:hover::before {
		transform: translateX(-10%);
	}
}

.rt_nav_right {
	&::before {
		border-left: 1rem solid #333;
		right: 0;
	}

	&:hover::before {
		transform: translateX(10%);
	}
}